import {
  HashRouter as Router,
  Switch,
  Route
} from "react-router-dom";

import Header from './components/common/header';
import Footer from './components/common/footer';
import Home from './pages/home';

function App() {
  return (
    <div className="App">
      <Router>
        <Header></Header>
        <Switch>
          <Route path="/">
            <Home></Home>
          </Route>
        </Switch>
        <Footer></Footer>
      </Router>
    </div>
  );
}

export default App;
