import sty from './index.module.scss';
import bgImg from '../../imgs/nav-bg.png';
import cn from 'classnames';
import fillIcon from '../../imgs/fill-icon2.png';
import fillIconTwo from '../../imgs/fill-icon1.png';
import fillIconThree from '../../imgs/fill-icon4.png';
import fillIconFour from '../../imgs/fill-icon3.png';
import cardOne from '../../imgs/card1.jpg';
import cardTwo from '../../imgs/card2.png';
import cardThree from '../../imgs/card3.png';
import { useEffect } from 'react';
import { WOW } from 'wowjs';
function Home() {

    useEffect(() => {
        var wow = new WOW({
            boxClass: "wow",    //需要执行动画的元素的 class
            animateClass: "animated", // animation.css 动画的 class
            offset: 0, // 距离可视区域多少开始执行动画
            mobile: true, // 是否在移动设备上执行动画
            live: true // 异步加载的内容是否有效
        })
        wow.init();
    }, []);

    return (
        <div className={sty.home}>
            <div className={sty.nav}>
                <img className={sty.bg} src={bgImg} alt="" />
                <div className={sty.outer}>
                    <div className={sty.title}>FOR ETHERRUM</div>
                    <div className={sty.subTitle}>ID Protocol</div>
                    <div className={sty.desc}>At presnet,the address on the chain is in the form of an island,and the operation of the user on the chain and the level of actitity do not have any impact on the user himself.How to generate identity for the user and let the user associate with different addresses ? is the problem we have to solve.</div>
                </div>
            </div>
            <div className={cn(sty.show, 'flex')}>
                <div className={cn(sty.item, 'flex-1 wow bounceInLeft')}>
                    <img src={fillIcon} alt="" />
                    <div className={sty.desc}>True gaming</div>
                </div>
                <div className={cn(sty.item, 'flex-1 wow bounceInLeft')}>
                    <img src={fillIconTwo} alt="" />
                    <div className={sty.desc}>High Dynamic Range</div>
                </div>
                <div className={cn(sty.item, 'flex-1 wow bounceInRight')}>
                    <img src={fillIconThree} alt="" />
                    <div className={sty.desc}>Frame per second</div>
                </div>
                <div className={cn(sty.item, 'flex-1 wow bounceInRight')}>
                    <img src={fillIconFour} alt="" />
                    <div className={sty.desc}>Velocity Architecture</div>
                </div>
            </div>
            <div className={sty.info}>
                <div className={sty.title}>
                    <span>Project </span>
                    <span className={sty.s}>Intergation</span>
                </div>
                <div className={cn(sty.top, 'flex-m')}>
                    <div className={cn(sty.side, 'wow bounceInLeft')}>
                        <div className={sty.title}>Crypto</div>
                        <div className={sty.subtitle}>Universe</div>
                        <div className={sty.desc}>
                            CRYPTO UNIVERSE(CU) IS THE NFT BASED ON BLOCKCHAIN DID WITH DEFI+GACHA+UNIQUE CARD COLLECTION.
                        </div>
                    </div>
                    <div className={cn(sty.right, 'wow bounceInRight')}>
                        <img className={sty.card1} src={cardOne} alt="" />
                        <img className={sty.card2} src={cardTwo} alt="" />
                        <img className={sty.card3} src={cardThree} alt="" />
                    </div>
                </div>
                <div className={cn(sty.box, 'flex-m')}>
                    <div className={cn(sty.left, 'wow bounceInLeft')}>
                        <img src={cardOne} alt="" />
                    </div>

                    <div className={cn(sty.side, 'wow bounceRight')}>
                        <div className={sty.title}>Coming</div>
                        <div className={sty.subtitle}>soon~</div>
                        <div className={sty.desc}>
                            At presnet,the address on the chain is in the form of an island,and the operation of the user on the chain and the level of actitity do not have any impact on the user himself.How to generate identity for the user and let the user associate with different addresses?is the problem we have to solve.
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Home;
