import sty from './index.module.scss';
import cn from 'classnames';
import footIconOne from '../../../imgs/twitter-logo.png';
import footIconTwo from '../../../imgs/github-logo.png';
import footIconThree from '../../../imgs/ins-logo.png';

function Footer() {
    return (
      <div className={cn(sty.footer)}>
        <div className={cn(sty.outer, 'tc')}>
            <a className={sty.about} href="">
                <img src={footIconOne} alt="" />
            </a>
            <a className={sty.about} href="">
                <img src={footIconTwo} alt="" />
            </a>
            <a className={sty.about} href="">
                <img src={footIconThree} alt="" />
            </a>
        </div>
      </div>
    );
  }
  
  export default Footer;
  